"use client"

import { useState } from "react"
import { signIn } from "next-auth/react"
import Link from "next/link"
import { useRouter, useSearchParams } from "next/navigation"
import { Truck, ArrowLeft, Phone, Lock, Eye, EyeOff } from "lucide-react"
import { Button, Input } from "@/components/ui"
import toast from "react-hot-toast"

export default function DeliveryLoginPage() {
  const [phone, setPhone] = useState("")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [loading, setLoading] = useState(false)
  const router = useRouter()
  const searchParams = useSearchParams()
  const error = searchParams.get("error")

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!phone || !password) {
      toast.error("Please enter phone number and password")
      return
    }

    // Validate phone number
    const cleanPhone = phone.replace(/\D/g, "").slice(-10)
    if (cleanPhone.length !== 10) {
      toast.error("Please enter a valid 10-digit phone number")
      return
    }

    setLoading(true)

    try {
      const result = await signIn("delivery-credentials", {
        phone: cleanPhone,
        password,
        redirect: false,
      })

      if (result?.error) {
        toast.error(result.error)
      } else {
        router.push("/delivery")
        router.refresh()
      }
    } catch (err) {
      toast.error("An error occurred. Please try again.")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-primary-50/60 to-white flex flex-col">
      {/* Header */}
      <header className="p-4 md:p-6">
        <Link
          href="/"
          className="inline-flex items-center gap-2 text-neutral-500 hover:text-neutral-900 transition-colors text-sm font-medium"
        >
          <ArrowLeft className="h-4 w-4" />
          <span>Back to home</span>
        </Link>
      </header>

      {/* Main Content */}
      <main className="flex-1 flex items-center justify-center p-4 pb-12">
        <div className="w-full max-w-sm">
          {/* Logo */}
          <div className="text-center mb-8">
            <div className="inline-flex items-center justify-center h-14 w-14 bg-primary-600 rounded-xl mb-4 shadow-lg shadow-primary-600/20">
              <Truck className="h-7 w-7 text-white" />
            </div>
            <h1 className="text-xl font-bold text-neutral-900">Delivery Portal</h1>
            <p className="text-neutral-500 mt-1 text-sm">Sign in to view your deliveries</p>
          </div>

          {/* Error Message */}
          {error && (
            <div className="mb-5 p-3.5 bg-red-50 border border-red-100 rounded-lg text-red-600 text-sm">
              Invalid credentials. Please try again.
            </div>
          )}

          {/* Login Form */}
          <form onSubmit={handleSubmit} className="bg-white rounded-xl border border-neutral-200 p-5 shadow-sm">
            <div className="space-y-4">
              <Input
                label="Phone Number"
                type="tel"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                placeholder="Enter your phone number"
                leftIcon={<Phone className="h-5 w-5" />}
                hint="Use the phone number provided by admin"
                autoComplete="tel"
                required
              />

              <Input
                label="Password"
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Enter your password"
                leftIcon={<Lock className="h-5 w-5" />}
                rightIcon={
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="focus:outline-none"
                  >
                    {showPassword ? (
                      <EyeOff className="h-5 w-5" />
                    ) : (
                      <Eye className="h-5 w-5" />
                    )}
                  </button>
                }
                autoComplete="current-password"
                required
              />
            </div>

            <Button
              type="submit"
              loading={loading}
              fullWidth
              size="lg"
              className="mt-6"
            >
              Sign In
            </Button>
          </form>

          {/* Help Text */}
          <div className="mt-5 p-3.5 bg-neutral-50 rounded-lg border border-neutral-100">
            <p className="text-xs text-neutral-500">
              <span className="font-semibold text-neutral-600">Need help?</span> Contact your administrator if you've forgotten your 
              credentials or need to update your phone number.
            </p>
          </div>
        </div>
      </main>
    </div>
  )
}
